.annoucement
{
    background: #0c0c0c;
    color: #fff;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 0;
    font: 0.8rem;
    border-bottom: 1px solid #262626;
}

.announcement-text
{
    display: flex;
    align-items: center;
    margin-right: 2rem;
}

.text1 span{
    margin-right: 2rem;
}

.text2 span:not(:last-child)
{
    margin-right: 1rem;
}

.text2 span:last-child
{
    margin-right: 1rem;
}
.announcement___companyName
{
    color: #BB86FC;
}