.review__box
{
    width: 90%;
    max-width: 550px;
    height: 230px;
    box-shadow: -10px 8px 40px #321f4975;
    position: relative;
}

.card
{
    height: 230px;
    padding: 40px;
    color: #fff;
    line-height: 22px;
    box-sizing: border-box;
    background: #1f1e1e85;
    position: relative;
    font-family: sans-serif;
    letter-spacing: 0.8px;
}

.profile
{
    display: flex;
    align-items: center;
    margin-bottom: 40px;
}

p{
    font-size: 15px;
    font-weight: 400;
    font-family: 'Courier New', Courier, monospace;
}


.profile img{
    width:70px;
    border-radius: 50%;
    margin-right: 20px;
}

.profile h3{
    font-size: 26px;
    color: #6832aa;
    margin-bottom: 8px;
}

.sidebar
{
    width: 35px;
    height: 100%;
    padding: 15px 0;
    box-sizing: border-box;
    background-color: #6832aa;
    position: absolute;
    top: 0;
    right: 0;
}

.reviews
{
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    border-radius: 25px;
}