.fadeBottom
{
    height: 9.7rem;
    background-image: linear-gradient(
        180deg, transparent, rgba(21, 11, 28, 0.61),#0e0a0e
    );
}
.fadeBottom1
{
  height: 9.7rem;
  background-image: linear-gradient(
      180deg, transparent, rgba(35, 18, 49, 0.61),#000000
  );
  position: relative;
  top: -5rem;
}

strong{
    color:#6832aa;
}
.typed-cursor
{
    display: none;
}
.bg-black
{
    background: #000;
}

.text__light__purple
{
    color: #6832aa;
}

.header__logo
{
    display: block;
    position: relative;
    top: 5rem;
    padding: 0 2rem;
}

.banner__logo
{
    position: relative;
    left: 5rem;
    font-family: sans-serif;
    font-size: 34px;
    letter-spacing: 4px;
    font-weight: 700;
}


.hero__banner__contents
{
    color: whitesmoke;
    font-family: sans-serif;
    display: flex;
    justify-content: space-between;
    margin: 8em 5em 3em 10em;
    padding: 3rem 0;
}

.banner__img > img
{
    width: 30rem;
    height: auto;
    position: relative;
}


.banner__text > h1
{
    font-size: 75px;
    font-weight: 600;
    padding-bottom: 5px;
}

.text__line__2
{
    font-family: sans-serif;
    font-size: 23px;
    font-weight: 100;
    color: whitesmoke ;
    padding-bottom: 7px;
}

.btn__transparent
{
    color: white;
    width: fit-content;
    background:transparent;
    border: 2px solid #6832aa;
    margin: 0.7rem 0;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 9px 18px;
    border-radius: 25px;
    font-size: 17px;
    font-family: sans-serif;
    box-shadow: 0 0 5px #6832aa, 0 0 25px #6832aa;
    transition: 500ms ease;
}

.btn__transparent:hover
{
    box-shadow: 0 0 5px #6832aa,
    0 0 25px #683268, 0 0 200px #683268, 0 0 200px #683268;
    background: #6832aa80;
    color: antiquewhite;
    transition: 500ms ease;
}

h1
{
    font-size: 55px;
}


.banner__text
{
    display: flex;
    flex-direction: column;
    justify-content: end;
    position: relative;
    top: 3rem;
    padding-right: 2em;
    height: 200px;
}

@media screen and (min-width:1061px) {
    .hero__banner__contents
    {
        width: 100%;
        margin: 6rem 5rem 5rem 7rem;
    }

    .banner__logo
    {
        color:white;
    }

    .banner__img > img
    {
        
        left: -10em;
        width: 35rem;
        top: 3rem;
    }

    .text__line__2
    {
        font-size: 18px;
        width: 80%;
        padding: 15px 0;
        font-weight: 400;
    }

    h1
    {
        font-size: 60px;
    }
}

@media screen  and (min-width: 980px) and (max-width: 1060px) {

    .hero__banner__contents
    {
        width: 100%;
        margin: 4rem 5rem 3rem 7rem;
    }

    h1
    {
        font-size: 48px;
        padding: 5px;
    }
    
    h4
    {
        color:white;
    }

    .text__line__2
    {
        font-size: 17px;
        line-height: 1.4rem;
        padding: 10px 5px;
    }

    .banner__img > img
    {
        
        left: -15em;
        width: 23em;
        top:3em;
    }
}

@media screen and (min-width:890px) and (max-width:980px) {
    h1
    {
        font-size: 45px;
    }

    .text__line__2
    {
        font-size: 13px;
    }

    .hero__banner__contents
    {
        width: 100%;
        margin: 4rem 5rem 2rem 3rem;
    }

    .banner__logo
    {
        color: white;
        left: 8rem;
    }

    .banner__text
    {
        left: 8rem;
    }

    .banner__img > img{
        width: 24rem;
        position: relative;
        left: -12rem;
    }
}

@media screen and (min-width:550px) and (max-width:889px) {
    .hero__banner__contents
    {
        flex-direction: column;
        position: relative;
        margin: 3rem 0 0 16rem;
    }

    h1
    {
        font-size: 43px;
    }

    .text__line__2
    {
        font-size: 17px;
        padding: 10px 0;
    }

    .banner__logo
    {
        color: antiquewhite;
        left: 15rem;
    }

    .banner__text
    {
        left: 5rem;
    }

    .banner__img > img{
        width: 24rem;
        position: relative;
        left: 8rem;
        top: 5rem;
    }
}

@media screen  and (max-width:549px){

    .hero__banner__contents
    {
        flex-direction: column;
        position: relative;
        margin: 3rem 0 0 17.5rem;
    }

    h1
    {
        font-size: 32px;
        padding: 5px;
    }

    .text__line__2
    {
        font-size: 15px;
        width: 350px;
        font-weight: 100;
        padding: 10px 5px;
        line-height: 1.4rem;
    }

    .banner__logo
    {
        left: 23.5rem;
        color: white;
    }

    .banner__text
    {
        left: 8rem;
    }

    .banner__img > img{
        width: 21rem;
        position: relative;
        left:8rem;
        top: 6rem;
    }
}





