.book_now
{
    width: 85%;
    color:white;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.bf__title
{
    color: #6832aa;
}
form
{
    width: 90%;
    height: fit-content;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    margin: 3rem;
    background-color: #21212170;
    padding: 2rem;
    border-radius: 18px;

}

label, input, p
{
    margin: 6px 0;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-size: 17px;
    letter-spacing: 1px;
    text-transform: capitalize;
    margin: 3px;
    padding: 4px;
}

button{
    margin: 10px 0;
    cursor: pointer;
    padding: 8px;
    background-color: #6832aa;
    border: none;
    font-size: 15px;
    color: white;
    border-radius: 12px;
    transition: 500ms ease-out;
}

input
{
    border-radius: 8px;
    border: none;
}

button:hover
{
    box-shadow: 0 0 5px #6832aa, 0 0 18px #6832aa;
    transition: 500ms ease-in;
}

h2
{
    font-size: 3rem;
    text-align: center;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-weight: 600;
}

.booknow__form
{
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
}

.appointment__img
{
    width: 25rem;
    margin: 3rem;
}

.form{
    position: relative;
    left: -2rem;
}

@media screen  and (max-width:980px){
    .appointment__img
    {
        margin: 2rem;
    }
}
@media screen  and (max-width:890px){
    .booknow__form
    {
        flex-direction: column;
    } 
}

@media screen and (max-width:450px) {
    .appointment__img
    {
        width: 20rem;
    }
}